t: 50%;\n        transform: translate(-50%, -50%);\n        background: #fff;\n        padding: 20px;\n        border-radius: 5px;\n        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);\n}\n", "",{"version":3,"sources":["webpack://./src/App.vue"],"names":[],"mappings":";AA2NA;QACA,aAAA;QACA,sBAAA;QACA,YAAA;QACA,aAAA;AACA;AAEA;QACA,aAAA;QACA,sBAAA;QACA,YAAA;QACA,WAAA;EACA,qBAAA;EACA,yBAAA;AACA;AAEA;QACA,YAAA;QACA,gBAAA;AACA;AAEA;QACA,aAAA;QACA,8BAAA;QACA,2BAAA;EACA,UAAA;AACA;AAEA;QACA,oBAAA;QACA,yBAAA;QACA,kBAAA;QACA,kBAAA;QACA,YAAA;AACA;AAEA;QACA,sBAAA;QACA,yBAAA;QACA,kBAAA;QACA,kBAAA;QACA,YAAA;AACA;AAEA;QACA,YAAA;AACA;AAEA;QACA,gBAAA;AACA;AAEA;KACA,aAAA;KACA,sBAAA;AACA;AAEA;EACA,iBAAA;EACA,kBAAA;AACA;AAEA;EACA,aAAA;EACA,mBAAA;EACA,mBAAA;AACA;AAEA;EACA,eAAA;EACA,gBAAA;EACA,mBAAA;AACA;AAEA;;EAEA,YAAA;AACA;AAEA;EACA,YAAA;EACA,iBAAA;AACA;AAEA;EACA,aAAA;EACA,sBAAA;EACA,iBAAA;EACA,gBAAA;EACA,YAAA;AACA;AAEA;QACA,eAAA;QACA,QAAA;QACA,SAAA;QACA,gCAAA;QACA,gBAAA;QACA,aAAA;QACA,kBAAA;QACA,2CAAA;AACA","sourcesContent":["<template>\n    <!--\n    SPDX-FileCopyrightText: Gesina Sands <gesina.sands@gmail.com>\n    SPDX-License-Identifier: AGPL-3.0-or-later\n    -->\n\t<div id=\"content\" class=\"app-nextcloudgpt\">\n        <Modal v-if=\"showModal\" @close=\"showModal = false\">\n    \t\t<div id=\"config-container\">\n\t\t\t\t<h2>{{ t('nextcloudgpt', 'OpenAI Configuration') }}</h2>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"api-key\">{{ t('nextcloudgpt', 'API Key') }}</label>\n\t\t\t\t\t<input id=\"api-key\" v-model=\"apiKey\" type=\"text\">\n\t\t\t\t</div>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"model\">{{ t('nextcloudgpt', 'Model') }}</label>\n\t\t\t\t\t<select id=\"model\" v-model=\"selectedModel\">\n\t\t\t\t\t\t<option value=\"gpt-4\">GPT-4</option>\n\t\t\t\t\t\t<option value=\"gpt-3.5-turbo\">GPT-3.5-turbo</option>\n\t\t\t\t\t</select>\n\t\t\t\t</div>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"top-p\">{{ t('nextcloudgpt', 'Top P') }}</label>\n\t\t\t\t\t<input id=\"top-p\" v-model=\"topP\" type=\"range\" min=\"0\" max=\"1\" step=\"0.01\">\n\t\t\t\t</div>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"freq-penalty\">{{ t('nextcloudgpt', 'Frequency Penalty') }}</label>\n\t\t\t\t\t<input id=\"freq-penalty\" v-model=\"frequencyPenalty\" type=\"range\" min=\"0\" max=\"1\" step=\"0.01\">\n\t\t\t\t</div>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"max-length\">{{ t('nextcloudgpt', 'Max Length') }}</label>\n\t\t\t\t\t<input id=\"max-length\" v-model=\"maxLength\" type=\"range\" min=\"1\" max=\"2048\" step=\"1\">\n\t\t\t\t</div>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"pres-penalty\">{{ t('nextcloudgpt', 'Presence Penalty') }}</label>\n\t\t\t\t\t<input id=\"pres-penalty\" v-model=\"presencePenalty\" type=\"range\" min=\"0\" max=\"1\" step=\"0.01\">\n\t\t\t\t</div>\n\t\t\t\t<div class=\"input-group\">\n\t\t\t\t\t<label for=\"token-length\">{{ t('nextcloudgpt', 'Token Length') }}</label>\n\t\t\t\t\t<input id=\"token-length\" v-model=\"tokenLength\" type=\"range\" min=\"1\" max=\"4096\" step=\"1\">\n\t\t\t\t</div>\n\t\t\t\t<form @submit.prevent=\"saveConfig\" style=\"display: contents;\">\n\t\t\t\t\t<button class=\"primary\">{{ t('nextcloudgpt', 'Save') }}</button>\n\t\t\t\t</form>\n\t\t\t</div>\n\t\t</Modal>\n\t\t<Modal v-if=\"isLoading\">\n\t\t\t<div class=\"loading-indicator\">\n        \t\tGenerating response...\n    \t\t</div>\n\t\t</Modal>\n\t\t<AppNavigation>\n\t\t\t<ActionButton icon=\"icon-settings\" @click=\"showModal = true\">\n\t\t\t\t{{ t('nextcloudgpt', 'API Config') }}\n\t\t\t</ActionButton>\n\t\t\t<div class=\"settings-display\">\n\t\t\t\t<div><strong>{{ t('nextcloudgpt', 'Current Settings:') }}</strong></div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'API Key') }}: {{ apiKey ? '*****' + apiKey.slice(-5) : 'Not set' }}</div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'Model') }}: {{ selectedModel }}</div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'Top P') }}: {{ topP }}</div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'Frequency Penalty') }}: {{ frequencyPenalty }}</div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'Max Length') }}: {{ maxLength }}</div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'Presence Penalty') }}: {{ presencePenalty }}</div>\n\t\t\t\t<div>{{ t('nextcloudgpt', 'Token Length') }}: {{ tokenLength }}</div>\n\t\t\t</div>\n\t\t\t<ActionButton icon=\"icon-delete\" @click=\"deleteAllMessages\">\n\t\t\t\t{{ t('nextcloudgpt', 'Delete All Messages') }}\n\t\t\t</ActionButton>\n\t\t\t<div>\n\t\t\t\t<h3>All Notes</h3>\n\t\t\t\t<ul>\n\t\t\t\t<li v-for=\"note in notes\" :key=\"note.id\">\n\t\t\t\t\t{{ note.title }}\n\t\t\t\t</li>\n\t\t\t\t</ul>\n\t\t\t</div>\n\t\t</AppNavigation>\n\t\t<AppContent>\n            <div id=\"chat-container\">\n                <div class=\"messages-container\">\n                    <div v-for=\"(message, index) in messages\" :key=\"index\" :class=\"message.role\">\n\t\t\t\t\t\t{{ message.message }}\n\t\t\t\t\t</div>\n                </div>\n                <div class=\"input-container\">\n                    <input ref=\"userInput\" v-model=\"userInput\" type=\"text\" @keyup.enter=\"sendMessage\">\n                    <button @click=\"sendMessage\">{{ t('nextcloudgpt', 'Send') }}</button>\n                </div>\n            </div>\n\t\t</AppContent>\n\t</div>\n\n</template>\n\n<script>\nimport ActionButton from '@nextcloud/vue/dist/Components/ActionButton'\nimport AppContent from '@nextcloud/vue/dist/Components/AppContent'\nimport AppNavigation from '@nextcloud/vue/dist/Components/AppNavigation'\nimport Modal from '@nextcloud/vue/dist/Components/Modal'\n\nimport '@nextcloud/dialogs/styles/toast.scss'\nimport { generateUrl } from '@nextcloud/router'\nimport { showError, showSuccess } from '@nextcloud/dialogs'\nimport axios from '@nextcloud/axios'\n\nexport default {\n\tname: 'App',\n\tcomponents: {\n\t\tActionButton,\n\t\tAppContent,\n\t\tAppNavigation,\n        Modal,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tshowModal: false,\n\t\t\tapiKey: '',\n\t\t\tselectedModel: 'gpt-4',\n\t\t\ttopP: 0.9,\n\t\t\tfrequencyPenalty: 0,\n\t\t\tmaxLength: 2048,\n\t\t\tpresencePenalty: 0,\n\t\t\ttokenLength: 4096,\n\t\t\tmessages: [],\n\t\t\tuserInput: '',\n\t\t\tisLoading: false\n\t\t}\n\t},\n\t/**\n\t * Fetch list of messages when the component is loaded\n\t */\n\tasync mounted() {\n\t\ttry {\n\t\t\tconst response = await axios.get(generateUrl('/apps/nextcloudgpt/messages'));\n\t\t\tfor (let i = 0; i < response.data.length; i++) {\n\t\t\t\tthis.messages.push(response.data[i]);\n\t\t\t}\n\t\t\tawait this.loadConfig();\n\t\t} catch (e) {\n\t\t\tconsole.error(e);\n\t\t\tshowError(t('nextcloudgpt', 'Could not fetch messages'));\n\t\t}\n\t\tthis.loading = false\n\t},\n\tmethods: {\n\t\tasync deleteAllMessages() {\n\t\t\ttry {\n\t\t\t\tthis.isLoading = true;\n\t\t\t\tconst response = await axios.delete(generateUrl('/apps/nextcloudgpt/delete-all'));\n\t\t\t\tthis.messages = [];\n\t\t\t\tthis.isLoading = false;\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e);\n\t\t\t\tshowError(t('nextcloudgpt', 'Could not delete all messages'));\n\t\t\t}\n\t\t},\n\t\tasync loadConfig() {\n\t\t\ttry {\n\t\t\t\tconst response = await axios.get(generateUrl('/apps/nextcloudgpt/openai_configs'));\n\t\t\t\tif (response.data.length > 0) {\n\t\t\t\t\tlet config  = response.data[0];\n\t\t\t\t\tthis.apiKey = config.apiKey;\n\t\t\t\t\tthis.selectedModel = config.selectedModel;\n\t\t\t\t\tthis.topP = config.topP;\n\t\t\t\t\tthis.frequencyPenalty = config.frequencyPenalty;\n\t\t\t\t\tthis.maxLength = config.maxLength;\n\t\t\t\t\tthis.presencePenalty = config.presencePenalty;\n\t\t\t\t\tthis.tokenLength = config.tokenLength;\n\t\t\t\t}\n\t\t\t} catch (error) {\n\t\t\t\tconsole.error(error);\n\t\t\t}\n\t\t\t},\n\t\tasync saveMessage(message, role) {\n\t\t\tthis.isLoading = true;\n\t\t\ttry {\n\t\t\t\tconst response = await axios.post(generateUrl('/apps/nextcloudgpt/messages'), {\n\t\t\t\t\tmessage: message,\n\t\t\t\t\trole: role\n\t\t\t\t});\n\t\t\t\t// assuming response.data is an array of messages\n\t\t\t\tthis.messages.push(...response.data);\n\t\t\t} catch (e) {\n\t\t\t\tconsole.error(e);\n\t\t\t\tshowError(t('nextcloudgpt', 'Could not save the message'));\n\t\t\t}\n\t\t\tthis.isLoading = false;\n    \t},\n        async saveConfig() {\n\t\t\t// Save API key and model configuration here\n\t\t\t// localStorage.setItem('apiKey', this.apiKey)\n\t\t\t// Example: localStorage.setItem('selectedModel', this.selectedModel)\n\t\t\t// Save other settings as needed\n\t\t\ttry {\n\t\t\t\tconst config = {\n\t\t\t\tapiKey: this.apiKey,\n\t\t\t\tselectedModel: this.selectedModel,\n\t\t\t\ttopP: this.topP,\n\t\t\t\tfrequencyPenalty: this.frequencyPenalty,\n\t\t\t\tmaxLength: this.maxLength,\n\t\t\t\tpresencePenalty: this.presencePenalty,\n\t\t\t\ttokenLength: this.tokenLength,\n\t\t\t\t};\n\t\t\t\tawait axios.put(generateUrl('/apps/nextcloudgpt/openai_configs'), config);\n\t\t\t} catch (error) {\n\t\t\t\tconsole.error(error);\n\t\t\t}\n\t\t\tthis.showModal = false\n\t\t},\n        sendMessage() {\n\t\t\t// Send user input to backend and display response\n\t\t\tthis.saveMessage(this.userInput, 'user');\n\t\t\tthis.userInput = '';\n\t\t\t// Example: get bot response and update messages array\n\t\t\t// this.messages.push({ sender: 'bot', text: 'Bot response' });\n\t\t},\n\t},\n}\n</script>\n<style scoped>\n    #app-content > div {\n        display: flex;\n        flex-direction: column;\n        flex-grow: 1;\n        padding: 20px;\n    }\n\n    #chat-container {\n        display: flex;\n        flex-direction: column;\n        height: 100%;\n        width: 100%;\n\t\tmargin: 3rem 0 3rem 0;\n\t\tborder: #a4a4a4 1px solid;\n    }\n\n    .messages-container {\n        flex-grow: 1;\n        overflow-y: auto;\n    }\n\n    .input-container {\n        display: flex;\n        justify-content: space-between;\n        margin: 2rem auto 3rem auto;\n\t\twidth: 75%;\n    }\n\n    .user {\n        align-self: flex-end;\n        background-color: #e0e0e0;\n        border-radius: 5px;\n        margin-bottom: 5px;\n        padding: 5px;\n    }\n\n    .bot {\n        align-self: flex-start;\n        background-color: #f0f0f0;\n        border-radius: 5px;\n        margin-bottom: 5px;\n        padding: 5px;\n    }\n\n    input[type='text'] {\n        flex-grow: 1;\n    }\n\n    button {\n        margin-left: 5px;\n    }\n\n\t#config-container {\n    \tdisplay: flex;\n    \tflex-direction: column;\n\t}\n\n\t#config-container > h2 {\n\t\tmargin-left: auto;\n\t\tmargin-right: auto;\n\t}\n\n\t.input-group {\n\t\tdisplay: flex;\n\t\talign-items: center;\n\t\tmargin-bottom: 10px;\n\t}\n\n\t.input-group label {\n\t\tflex: 0 0 200px;\n\t\ttext-align: left;\n\t\tmargin: 0 2em 0 2em;\n\t}\n\n\t.input-group input[type=\"text\"],\n\t.input-group select {\n\t\tflex-grow: 1;\n\t}\n\n\t.input-group input[type=\"range\"] {\n\t\tflex-grow: 1;\n\t\tmargin-left: 10px;\n\t}\n\n\t.settings-display {\n\t\tdisplay: flex;\n\t\tflex-direction: column;\n\t\tfont-size: 0.8rem;\n\t\tmargin-top: 10px;\n\t\tpadding: 5px;\n\t}\n\n\t.loading-indicator {\n        position: fixed;\n        top: 50%;\n        left: 50%;\n        transform: translate(-50%, -50%);\n        background: #fff;\n        padding: 20px;\n        border-radius: 5px;\n        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);\n    }\n</style>\n"],"sourceRoot":""}]);
